import type * as Types from './types';

import { gql } from '@apollo/client';
import * as Apollo from '@apollo/client';
const defaultOptions = {} as const;
export type ChainQueryVariables = Types.Exact<{ [key: string]: never; }>;


export type ChainQuery = { __typename: 'Query', chain: { __typename: 'ChainInfo', name: string, baseChainHeight: string, peerCount: number, consensusParameters: { __typename: 'ConsensusParameters', gasPriceFactor: string } } };

export type Chain = { __typename: 'ChainInfo', name: string, baseChainHeight: string, peerCount: number, consensusParameters: { __typename: 'ConsensusParameters', gasPriceFactor: string } };

export const ChainFragmentDoc = gql`
    fragment Chain on ChainInfo {
  name
  baseChainHeight
  peerCount
  consensusParameters {
    gasPriceFactor
  }
}
    `;
export const ChainQueryDocument = gql`
    query ChainQuery {
  chain {
    ...Chain
  }
}
    ${ChainFragmentDoc}`;

/**
 * __useChainQuery__
 *
 * To run a query within a React component, call `useChainQuery` and pass it any options that fit your needs.
 * When your component renders, `useChainQuery` returns an object from Apollo Client that contains loading, error, and data properties
 * you can use to render your UI.
 *
 * @param baseOptions options that will be passed into the query, supported options are listed on: https://www.apollographql.com/docs/react/api/react-hooks/#options;
 *
 * @example
 * const { data, loading, error } = useChainQuery({
 *   variables: {
 *   },
 * });
 */
export function useChainQuery(baseOptions?: Apollo.QueryHookOptions<ChainQuery, ChainQueryVariables>) {
        const options = {...defaultOptions, ...baseOptions}
        return Apollo.useQuery<ChainQuery, ChainQueryVariables>(ChainQueryDocument, options);
      }
export function useChainQueryLazyQuery(baseOptions?: Apollo.LazyQueryHookOptions<ChainQuery, ChainQueryVariables>) {
          const options = {...defaultOptions, ...baseOptions}
          return Apollo.useLazyQuery<ChainQuery, ChainQueryVariables>(ChainQueryDocument, options);
        }
export type ChainQueryHookResult = ReturnType<typeof useChainQuery>;
export type ChainQueryLazyQueryHookResult = ReturnType<typeof useChainQueryLazyQuery>;
export type ChainQueryQueryResult = Apollo.QueryResult<ChainQuery, ChainQueryVariables>;